.image-carousel-container {
  /* border: 1px solid black; */
  /* width: 100vw; */
  margin-bottom: 30px;

}

.image-carousel {
  text-align: center;
}

.imgList {
  width: 80vw;
  scroll-behavior: smooth;
  display: inline-block;
  position: relative;
  vertical-align: middle;
  text-align: center;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
  margin: auto;
}

.image-carousel .imgList::-webkit-scrollbar {
  display: none;
}

.imgList-item {
  list-style: none;
  display: inline-block;
  position: relative;
  vertical-align: top;
  margin-left: 10px;
  margin-right: 10px;
  transition: 0.2s;
  /* width: 500px; */
  min-height: 350px;
}

.image-container {
  transition: 0.2s;
  width: 500px;
  max-height: 250px;
  overflow: hidden;
  border-radius: 10px;
}

.image-container img {
  width: 100%;
  border-radius: 10px;
  transition: transform 0.5s ease;
}

.imgList-item:hover img {
  transform: scale(1.1);
}

.imgList-item:hover .image-item-caption {
  transition: 0.2s;
  display: block;
}

.image-item-caption {
  display: none;
  width: 500px;
  background-size: cover;
  padding: 5px;
  color: rgb(0, 0, 0);
  position: relative;

  word-wrap: break-word;
  white-space: pre-wrap;
  word-break: break-word;
  /* background-color: rgb(255, 255, 255); */
  padding: 10px;
}

.image-item-caption p {
  font-size: 18px;
  max-height: 100px;
  max-width: 500px;
  overflow-wrap: break-word;
  word-wrap: break-word;
  text-align: left;
}

.image-carousel-btn {
  width: 40px;
  height: 40px;
  padding: 10px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  background-color: rgba(202, 202, 202, 0.7);
  font-size: 18px;
}

.image-carousel-btn:active {
  transform: scale(1.1);
}

@media (max-width: 767px) {
  .image-carousel-container {
    margin-bottom: 20px;
  }

  .imgList {
    width: 80%;
  }

  .imgList-item {
    min-height: 250px;
  }
  .imgList-item:hover img {
  transform: scale(1.05);
}
  .image-container {
    transition: 0.2s;
    width: 100%;
    max-height: 200px;
    overflow: hidden;
    border-radius: 10px;
  }

  .image-container img {
    width: 100%;
    border-radius: 10px;
    transition: transform 0.5s ease;
  }

  .image-item-caption p {
    font-size: 14px;
  }

  .image-carousel-btn {
    width: 35px;
    height: 35px;
    padding: 5px;
    font-size: 14px;
  }
}

@media (max-width: 500px) {

    .imgList {
    width: 80%;
  }

  .imgList-item {
    min-height: 200px;
    height: 300px;
    width: 100%;
  }
  .image-container {
    transition: 0.2s;
    height: 250px;
    max-height: 200px;
    overflow: hidden;
    border-radius: 10px;
    width: 100%;
  }

  .image-container img {
    width: 100%;
    height: 250px;
    border-radius: 10px;
    transition: transform 0.5s ease;
  }
  .image-item-caption {
    display: block;
    word-wrap: break-word;
    white-space: pre-wrap;
    word-break: break-word;
    /* height: 300px; */
    width: 100%;
  }
  
}