* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
font-family: 'Work Sans', sans-serif;
/* font-family: 'Raleway', sans-serif; */
}

:root {
  --theme-blue: #00AEEF;
  --background-color:#f8f9fa;
}

p {
  font-size: 16px;
  /* text-align: justify; */

}

a {
  text-decoration: none;
  color: black;
  font-size: 16px;
}
a:hover {
  color: #013b87;
}

.main-container {
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  /* margin: 10px; */
  background-color:var(--background-color);
}
.emptyList-wrap {
  display: flex;
  justify-content: center;
}
.emptyList-wrap img {
  max-width: 500px;
  width: 100%;
}

.page-goback{
  margin: 20px 0px;
}
.page-goback a{
  color: black;
}
.page-goback a:hover{
  color: rgb(0, 78, 122);
  
}



.grid-2{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 3rem;
}
.grid-3{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}
.grid-4{
  /* display: flex; */
  /* margin-top: 20px; */
  /* width: 100%; */
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2rem;
  /* margin: 2rem; */

}
.grid-1-3 {
  display: grid;
  grid-template-columns: 25% auto;
  grid-gap: 3rem;
}
.grid-3-1 {
  display: grid;
  grid-template-columns: auto 25% ;
  grid-gap: 3rem;
}
.grid-1-2 {
  display: grid;
  grid-template-columns: 33% auto;
  grid-gap: 3rem;
}







