.scroll-to-top {
  background-color: #ffffff;
  font-size: 30px;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50px;
  padding: 10px;
  position: fixed;
  bottom: 30px;
  right: 30px;
  z-index: 1000;
  cursor: pointer;
  display: none;
  box-shadow: 0 0 11px rgba(33,33,33,.5); 

}
.visible {
  display: block;
  transition: right 0.2s ease-in-out;

}



