.footer-section {
  background: #151414;
  position: relative;
}
.footer-container{
  margin: 10px 10vw;
}
.footer-cta {
  border-bottom: 1px solid #373636;
  padding: 20px 0px;
}

.footer-content {
  position: relative;
  z-index: 2;
  padding: 20px 0px;
}
.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%;
}
.footer-logo {
  margin-bottom: 30px;
}
.footer-logo img {
    max-width: 200px;
}
