*{
  color: black;
  font-family: 'Work Sans', sans-serif;
}

:root {
    --theme-blue: #00AEEF;
    --hover-blue:rgb(2, 41, 86);
    --theme-light-grey: rgb(255, 255, 255);
    --theme-dark-grey: rgb(237, 237, 237);
}
a{
    text-decoration: none;
    
}
a:hover{
  color: black;
  
}
.nav-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  margin: auto 10vw;
  
}

.navbar {
    background: var(--theme-light-grey);
    width: 100%;
    height: 60px;
    display: flex;
    align-items: center;
    z-index: 100;
    justify-content: space-between;
    top: 0;
    position: sticky;
    /* box-shadow:  5px 0px 10px 0px rgb(85, 84, 84); */
    box-shadow: 0px 5px 5px 0px rgb(176 177 193 / 10%);

}

.logo-image img{
    width: 250px;
    align-items: center;
    margin: auto;
}


.nav-logo {
    align-items: center;
    /* margin-left: 20px; */
    flex-grow: 1;

}

.nav-menu {
    display: flex;
    list-style: none;
    text-align: center;
    justify-content: space-between;

}

.nav-links {
    text-decoration: none;
    /* margin: 0 10px; */
    padding: 15px 0.5rem;
    font-weight: 500;
    /* font-family: 'Raleway', sans-serif; */
      font-size: 14px;
    align-items: center;
    border: none;
    cursor: pointer;

    
    
}
.nav-links:hover {
    /* border-bottom: 3px solid var(--theme-blue); */
    transition:100ms ease-in;
    text-decoration: none;

}



.active-nav{
    text-decoration: none;
    padding: 15px 0.5rem;
    /* margin: 0 10px; */
    font-weight: 500;
    /* font-family: 'Raleway', sans-serif; */
      font-size: 14px;
    align-items: center;
    /* border-bottom: 3px solid var(--theme-blue); */
    /* transition: all .5s; */

}
.active-nav:hover{
    text-decoration: none;
    

}

.nav-dropbtn{
    display: inline-block;
    font-weight: 500;
    /* font-family: 'Raleway', sans-serif; */
      font-size: 14px;
    border: none;
    background-color: var(--theme-light-grey);
    /* margin: 0 10px; */
    /* padding: 10px 0px; */
    text-decoration: none;
    align-items: center;
  }

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: var(--theme-light-grey);
    /* min-width: 200px; */
    z-index: 10;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
    border-radius: 5px 10px;
    border-top: 2px solid rgb(2, 33, 105);
    margin: 10px 10px;
    transition: 2s ease-in;

  }

.drop-nav-links {
    float: none;
    display: block;
    text-decoration: none;
      font-size: 14px;
    /* font-family: 'Raleway', sans-serif; */
    font-weight: 500;
    text-align: left;
    padding: 5px 10px;
    /* margin: 5px;     */
   
  }
  .active-drop{
    float: none;
    display: block;
    text-decoration: none;
      font-size: 14px;
    /* font-family: 'Raleway', sans-serif; */
    font-weight: 500;
    text-align: left;
    padding: 5px 10px;
    /* margin: 5px; */
    /* background-color:var(--theme-dark-grey); */
    /* color: var(--theme-blue); */

  }
  .active-drop:hover{
    text-decoration: none;
    color: var(--hover-blue);

  }
  .drop-nav-links:hover {
    /* background-color:var(--theme-dark-grey); */
    text-decoration: none;
    color: var(--hover-blue);
  }


  .nav-item:hover .dropdown-content{
    display: block;
    text-decoration: none;

    
  }
.fa-code {
    margin-left: 1rem;
}
.nav-item {
    line-height: 30px;
}

.nav-icon {
    display: none;
}













@media (max-width: 1300px){

    .logo-image img{
        width: 250px;
    }

    .nav-links {
        padding: 8x 0px;
        font-size: 12px;
        margin: 0 5px;
    
    }
    .nav-logo {
        margin-left: 10px;
        flex-grow: 1;
    }
    
    .active-nav{
        padding: 9px 0px;
        margin: 0 5px;
        font-size: 12px;    
    }

    .nav-dropbtn{
        font-size: 12px;
    
      }
    
    .drop-nav-links {
        font-size: 12px;
        padding: 0px 8px;
        
      }
      .active-drop{
        font-size: 12px;
        padding: 0px 8px;
       
      }
}








@media screen and (max-width: 1100px) {
    .nav-menu {
        display: flex;
        flex-direction: column;
        width: 100%;
        position: absolute;
        top: 62px;
        left: -110%;
        opacity: 1;
        transition: all 0.5s ease;
        box-shadow: 0 2px 2px rgba(0, 0, 0, 0.3);
        text-align: left;
        padding: 10px;
        background: var(--theme-light-grey);
        min-height: 70vh;
        overflow: scroll;
    }
    .nav-logo {
        margin-left: 0px;

    
    }
    .logo-image img{
        width: 250px;
    }

    .nav-menu.active {
        background-color: var(--theme-light-grey);
        border-bottom: none;
        left: 0px;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }



    .nav-item {
        margin-right: 0;
        background: var(--theme-light-grey);
    }

    .dropdown-content {
        display: none;
        position: relative;
        width: 100%;
        box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.1);
        z-index: 10;
        /* margin-top: 3px; */
        box-shadow: none;
        border-radius: 0 0 4px 4px;
        margin: 0 0px;
        
      }

    .nav-links {
        padding:0px 10px;
        width: 100%;
        display: table;
        margin: 0px;
        border-bottom: none;
        background: var(--theme-light-grey);
          font-size: 14px;
    }
    .nav-dropbtn{
        display: inline-block;
        font-weight: 500;
        /* font-family: 'Raleway', sans-serif; */
          font-size: 14px;
        border: none;
        background: var(--theme-light-grey);
        /* margin: 0 10px; */
        /* padding: 10px 0px; */

    
      }
      .drop-nav-links {
        float: none;
        /* width: 90vw; */
        display: block;
        text-decoration: none;
          font-size: 14px;
        /* font-family: 'Raleway', sans-serif; */
        font-weight: 500;
        text-align: left;
        padding: 0px 10px;
        margin: 5px; 
        margin-left: 25px;   
      }
      .active-drop{
        float: none;
        display: block;
        text-decoration: none;
          font-size: 14px;
        /* font-family: 'Raleway', sans-serif; */
        font-weight: 500;
        text-align: left;
        padding: 0px 10px;
        margin: 5px;
        /* background-color: var(--theme-dark-grey); */
        /* color: var(--theme-blue); */
        margin-left: 25px;   
      }
    .active-nav{
        padding:0px 10px;
        width: 100%;
        display: table;
        margin: 0px;
        border-bottom: none;
          font-size: 14px;
    
    }

    .nav-icon {
        display: block;
        position: absolute;
        top: 10;
        right: 20px;
        font-size: 30px;
        cursor: pointer;
        align-items: center;
        /* background-color:var(--theme-blue); */
        padding:5px 10px ;
        border-radius: 5px;
    }
    
}















@media screen and (max-width: 767px) {
    .logo-image img{
        width: 250px;
    }

    .nav-links {
          font-size: 14px;
    }
    .nav-dropbtn{
          font-size: 14px;
      }
      .drop-nav-links {
          font-size: 14px;
      }
      .active-drop{
          font-size: 14px;
      }
    .active-nav{
          font-size: 14px;
    
    }




}